01. common
*************************
*/

@import url('https://fonts.googleapis.com/css2?family=Barlow+Condensed:wght@500&display=swap');
body {
    font-family: 'Oswald', sans-serif;
    font-size: 15px;
    font-weight: 400;
    font-style: normal;
    color: #8f98a8;
}

.img,
img {
    max-width: 100%;
    -webkit-transition: all 0.3s ease-out 0s;
    -moz-transition: all 0.3s ease-out 0s;
    -ms-transition: all 0.3s ease-out 0s;
    -o-transition: all 0.3s ease-out 0s;
    transition: all 0.3s ease-out 0s;
}

.f-left {
    float: left;
}

.f-right {
    float: right;
}

.fix {
    overflow: hidden;
}

a {
    text-decoration: none;
}

a,
.button {
    -webkit-transition: all 0.3s ease-out 0s;
    -moz-transition: all 0.3s ease-out 0s;
    -ms-transition: all 0.3s ease-out 0s;
    -o-transition: all 0.3s ease-out 0s;
    transition: all 0.3s ease-out 0s;
}

a:focus,
.button:focus {
    text-decoration: none;
    outline: none;
}

a:focus,
a:hover {
    color: inherit;
    text-decoration: none;
}

a,
button {
    color: inherit;
    outline: medium none;
}

button:focus,
input:focus,
input:focus,
textarea,
textarea:focus {
    outline: 0;
}

.uppercase {
    text-transform: uppercase;
}

.capitalize {
    text-transform: capitalize;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    font-family: 'Barlow Condensed', sans-serif;
    color: #040021;
    margin-top: 0px;
    font-weight: 700;
    text-transform: normal;
    line-height: 1.16;
}

h1 a,
h2 a,
h3 a,
h4 a,
h5 a,
h6 a {
    color: inherit;
}

h1 {
    font-size: 90px;
    line-height: 1;
    letter-spacing: 0;
}

@media only screen and (min-width: 768px) and (max-width: 991px) {
    h1 {
        font-size: 50px;
    }
}

h2 {
    font-size: 48px;
    letter-spacing: 0;
    line-height: 58px;
}

@media only screen and (min-width: 992px) and (max-width: 1200px) {
    h2 {
        font-size: 40px;
        line-height: 48px;
    }
}

@media (max-width: 767px) {
    h2 {
        font-size: 25px;
        line-height: 32px;
    }
    nav#mobile-menu {
        display: none;
    }
}

h3 {
    font-size: 28px;
}

h4 {
    font-size: 24px;
    line-height: 58px;
}

@media only screen and (min-width: 992px) and (max-width: 1200px),
only screen and (min-width: 768px) and (max-width: 991px) {
    h4 {
        font-size: 22px;
    }
}

@media (max-width: 767px) {
    .customh3 {
        font-size: 2rem;
        padding-top: 25px;
        padding-left: 25px;
    }
    h4 {
        font-size: 22px;
        line-height: 28px;
    }
}

h5 {
    font-size: 18px;
}

h6 {
    font-size: 16px;
}

ul {
    margin: 0px;
    padding: 0px;
}

li {
    list-style: none;
}

p {
    font-size: 17px;
    font-weight: 400;
    line-height: 25px;
    color: #8f98a8;
    margin-bottom: 0;
}

hr {
    border-bottom: 1px solid #919191;
    border-top: 0 none;
    margin: px 0;
    padding: 0;
}

label {
    color: #2c2a3a;
    font-size: 14px;
    font-weight: 600;
}

*::-moz-selection {
    background: #040021;
    color: #ffffff;
    text-shadow: none;
}

*::selection {
    background: #040021;
    color: #ffffff;
    text-shadow: none;
}


/*--
    - Input Placeholder
-----------------------------------------*/

*::-moz-placeholder {
    color: #8f98a8;
    font-size: 15px;
    opacity: 0.7;
}

*::placeholder {
    color: #8f98a8;
    font-size: 15px;
    opacity: 0.7;
}


/*--
    - select
-----------------------------------------*/

select {
    color: #b9b9b9;
    font-size: 14px;
    opacity: 1;
    appearance: none;
}

#scrollUp {
    background: #213b60;
    height: 35px;
    width: 35px;
    right: 50px;
    bottom: 77px;
    color: #fff;
    font-size: 20px;
    text-align: center;
    line-height: 34px;
}

#scrollUp:hover {
    background: #8f98a8;
}


/*--
    - Common Classes
-----------------------------------------*/

.fix {
    overflow: hidden;
}

.clear {
    clear: both;
}

.p-relative {
    position: relative;
}

.p-absolute {
