.logo-shape {
    position: relative;
}

.logo-shape:before {
    position: absolute;
    content: "";
    width: 100%;
    height: 45px;
    background: #213b60;
    clip-path: polygon(100% 0, 100% 100%, 0 100%);
    top: -45px;
    left: 0;
}

.logo-shape:after {
    position: absolute;
    content: "";
    width: 35px;
    height: 45px;
    background: #4628ab;
    clip-path: polygon(0 0, 100% 100%, 0 100%);
    top: -45px;
    right: -35px;
}

