.progressbar {
  margin: 25px 20px 25px 0;
  counter-reset: step;
}
.progressbar li {
  list-style-type: none;
  width: 10.5%;
  float: left;
  font-size: 18px;
  position: relative;
  text-align: center;
  text-transform: uppercase;
  color: #16247d;
  z-index: 1;
}
.progressbar li:before {
  width: 15px;
  height: 15px;
  content: "";
  line-height: 30px;
  border: 2px solid #16247d;
  background-color: #16247d;
  display: block;
  text-align: center;
  margin: 0 auto 10px auto;
  border-radius: 50%;
  transition: all 0.8s;
  z-index: 998;
  position: relative;
}
.progressbar li:after {
  width: 100%;
  height: 2px;
  content: "";
  position: absolute;
  background-color: #fff;
  top: 7px;
  left: -43.5%;
  z-index: 0;
  transition: all 0.8s;
}
.progressbar li:first-child:after {
  content: none;
}
.progressbar li.active:before {
  border-color: #16247d;
  background-color: #16247d;
  transition: all 0.8s;
  z-index: 1;
}
.progressbar li.active:after {
  background-color: #16247d;
  transition: all 0.8s;
}

